import "../boethius/styles/index.css";
import WebFont from "webfontloader";
import Scored from "../boethius/src/Scored";
import compile from "../boethius-lang/src/main";

function createCanvas (el) {
    el.innerHTML = '<canvas class="scored-canvas parnassus"><canvas>';

    return document.getElementsByClassName("scored-canvas");
}


export function start (cb) {
    var canvas = createCanvas(document.getElementById("music"));
    var scored = new Scored();
    scored.setup(canvas[0]);
    var fontLoader = WebFont.load({
        custom: {
            families: ["gonville", "gonvillealpha"]
        },
        active: () => {cb(scored);}
    });
    scored.project.view.update();
}

function createSelect (el, items) {
    var select = document.createElement("select");

    for (let k in items) {
        var option = document.createElement("option");
        option.innerHTML = k;
        option.value = k;
        select.appendChild(option);
    }

    el.appendChild(select);

    return select;
}

export function run (scored) {
    const musicSelect = createSelect(document.getElementById("musicList"), musics);
    const layoutSelect = createSelect(document.getElementById("layoutList"), layouts);

    const {voices, chordSymbols} = compile(musics[musicSelect.value]);
    const music = musicToScoredObjects(voices);
    let score = renderScore(scored, JSON.parse(layouts[layoutSelect.value]), music, chordSymbols);
    score.translate(25, 50);

    musicSelect.onchange = function (e) {
        const val = e.target.value;
        // window.location.hash = example;
        if (score) score.remove();
        const {voices, chordSymbols} = compile(musics[val]);
        const music = musicToScoredObjects(voices);
        score = renderScore(scored, JSON.parse(layouts[layoutSelect.value]), music, chordSymbols);
        score.translate(25, 50);
    };

    layoutSelect.onchange = function (e) {
        const val = e.target.value;
        if (score) score.remove();
        const {voices, chordSymbols} = compile(musics[musicSelect.value]);
        const music = musicToScoredObjects(voices);
        score = renderScore(scored, JSON.parse(layouts[val]), music, chordSymbols);
        score.translate(25, 50);
    };
}

function renderScore (scored, layout, voices, chordSymbols) {
    return scored.render(layout, {
        voices: voices.map(Scored.parse),
        pages: [layout.currentPage],
        chordSymbols: chordSymbols.map(Scored.parse)
    });
}

/*
 * function for mapping over arbitrary object.
 */
function map (fn, obj) {
    const ret = [];
    for (let k in obj) {
        if (obj.hasOwnProperty(k)) {
            ret.push(fn(obj[k], k));
        }
    }
    return ret;
}

function createElement (type, props, children) {
    return {type, props, children};
}

function musicToScoredObjects (music) {
    return map((v, k) => {
        return createElement("voice", {name: k}, v);
    }, music);
}

start(run);



